.Calculatr {
  height: 400px;
  width: 300px;
 
}
.Buttonuu {
  
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.05);
  align-items: center;
  justify-content: center;
  width: 25%;
  font-size:24px;
  color: rgba(0, 0, 0, 0.5);
  height: 20%;
  min-width: 25%;
  
  background: #cad2c5;

}
.Buttons{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  height: 80%;
}
.dispalyss{
  background: #2B293D;
  color: #59C3C3;
  height: 20%;
  text-shadow: 0 0 5PX RGBA(89, 195, 195, 0.5);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 29px;
  box-sizing: border-box;
  overflow: hidden;
  font-size: 24px;
  position: relative;
}

.Buttonuu[data-size="2"] {
  height: 40%;
}

.Buttonuu[data-value="null"] {
  pointer-events: none;
}





